'use client';

import React from 'react';
import Image from 'next/image';
import { useTheme } from 'next-themes';

interface CertificateCardProps {
  title: string;
  subtitle: string;
  status: 'locked' | 'completed';
  image: string;
  lockedImage: string;
  titleLocked: string;
  titleUnlocked: string;
  descriptionLocked: string;
  descriptionUnlocked: string;
  shareUrl: string;
}

const CertificateCard: React.FC<CertificateCardProps> = ({
  title,
  subtitle,
  status,
  image,
  lockedImage,
  titleLocked,
  titleUnlocked,
  descriptionLocked,
  descriptionUnlocked,
  shareUrl,
}) => {
  const { theme } = useTheme();
  const isCompleted = status === 'completed';

  const iconStatus = `/assets/${theme === 'dark' ? 'dark' : 'light'}/${isCompleted ? 'unlocked.svg' : 'lock.svg'}`;

  const socialLinks = [
    {
      name: 'WhatsApp',
      url: `https://api.whatsapp.com/send?text=Mira mi certificado: ${shareUrl}`,
      icon: `/assets/${theme === 'dark' ? 'dark' : 'light'}/whatsapp.svg`,
    },
    {
      name: 'Facebook',
      url: `https://www.facebook.com/sharer/sharer.php?u=${shareUrl}`,
      icon: `/assets/${theme === 'dark' ? 'dark' : 'light'}/facebook.svg`,
    },
    {
      name: 'LinkedIn',
      url: `https://www.linkedin.com/sharing/share-offsite/?url=${shareUrl}`,
      icon: `/assets/${theme === 'dark' ? 'dark' : 'light'}/linkedin.svg`,
    },
  ];

  return (
    <div
      className={`relative p-6 rounded-xl flex flex-col text-left min-h-[500px] bg-[var(--background-2)] ${
        isCompleted ? 'opacity-100' : 'opacity-50'
      }`}
      style={{ marginBottom: '16px' }} // üëâ espaciado entre tarjetas
    >
      {/* Icono superior */}
      <div className="absolute top-6 right-6">
        <Image src={iconStatus} alt="Estado" width={30} height={30} />
      </div>

      {/* T√≠tulo principal del nivel */}
      <h2 className="p18 font-bold text-[var(--color-primary-900)] py-3">{title}</h2>
      <p className="p14 text-[var(--color-neutral-600)] mb-4">{subtitle}</p>

      {/* üîí BLOQUEADO */}
      {!isCompleted && (
        <>
          <div className="flex justify-center mb-4">
            <Image
              src={lockedImage}
              alt={`Certificado bloqueado ${title}`}
              width={280}
              height={200}
              className="object-contain"
            />
          </div>

          <p className="p20 font-bold text-[var(--color-primary-900)]">{titleLocked}</p>
          <p className="p16 text-[var(--color-primary-700)] mt-2">{descriptionLocked}</p>
        </>
      )}

      {/* ‚úÖ DESBLOQUEADO */}
      {isCompleted && (
        <>
          <p className="p20 font-bold text-[var(--color-primary-900)]">{titleUnlocked}</p>
          <p className="p16 text-[var(--color-primary-700)] mt-2">{descriptionUnlocked}</p>

          <div className="mt-4 flex justify-center">
            <Image
              src={image}
              alt={`Certificado ${title}`}
              width={280}
              height={200}
              className="rounded-md object-contain"
            />
          </div>

          <button className="mt-6 px-6 py-2 border border-[var(--primary-color)] text-[var(--primary-color)] text-sm font-bold rounded-lg hover:bg-[var(--primary-color)] hover:text-white transition">
            Descargar Certificado
          </button>

          <div className="mt-4">
            <p className="p16 font-bold text-[var(--color-neutral-600)] mb-2 text-center">Comp√°rtelo</p>
            <div className="flex gap-4 justify-center">
              {socialLinks.map(({ name, url, icon }) => (
                <a key={name} href={url} target="_blank" rel="noopener noreferrer">
                  <Image src={icon} alt={name} width={30} height={30} />
                </a>
              ))}
            </div>
          </div>
        </>
      )}
    </div>
  );
};

export default CertificateCard;
